<template lang='pug'>
    div.login-wrap
        .gm-title 客服管理系统
        .gm-login
            el-form.demo-ruleForm(ref='formCustom', :model='formCustom', :rules='ruleCustom')
                el-form-item(prop='username')
                    el-input(type='text', v-model='formCustom.username', placeholder='请输入用户名', autofocus)
                el-form-item(prop='passwd')
                    el-input(type='password', v-model='formCustom.passwd', placeholder='请输入密码', @keyup.enter.native='handleSubmit("formCustom")')
                el-form-item(prop='remberMe')
                    el-checkbox(v-model='remember') 记住密码
                el-form-item
                    el-button(type='success', @click='handleSubmit("formCustom")', size='medium', style='width: 100%;') 登录
                    //- Button(type='ghost', long, @click='handleReset("formCustom")') 重置

            //- Input.lg-item(v-model='userData.username', placeholder='username')
            //- Input.lg-item(v-model='userData.password', placeholder='password', @keyup.enter.native='submitForm')
            //- .login-btn.lg-item
            //-     Button(type='success', long, @click='submitForm') 登录
</template>

<script>
export default {
    data () {
        const validateUser = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('用户名不能为空'))
            } else {
                callback()
            }
        }
        const validatePass = (rule, value, callback) => {
            if (!value) {
                callback(new Error('请输入密码'))
            } else {
                callback()
            }
        }

        return {
            formCustom: {
                username: '',
                passwd: ''
            },
            ruleCustom: {
                username: [
                    {validator: validateUser, trigger: 'blur'}
                ],
                passwd: [
                    { validator: validatePass, trigger: 'blur' }
                ]
            },
            remember: true
        }
    },
    methods: {
        handleSubmit (name) {
            let _this = this
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$http.post('/users/token', {}, {
                        auth: {
                            username: this.formCustom.username,
                            password: this.formCustom.passwd
                        }
                    }).then(function (res) {
                        window.localStorage.setItem('Token', res.data.token)
                        _this.$message({
                            message: '登录成功',
                            type: 'success'
                        })
                        localStorage.setItem('gm_username', _this.formCustom.username)
                        if (_this.remember === true) {
                            localStorage.setItem('gm_passwd', _this.formCustom.passwd)
                        }
                        _this.$router.push('/home')
                    }).catch(function (error) {
                        console.log(error)
                    })
                } else {
                    this.$message({
                        message: '格式不正确',
                        type: 'error'
                    })
                }
            })
        },
        handleReset (name) {
            this.$refs[name].resetFields()
        }
    },
    created () {
        if (localStorage.gm_username) {
            this.formCustom.username = localStorage.gm_username
        }
        if (localStorage.gm_passwd) {
            this.formCustom.passwd = localStorage.gm_passwd
        }
    },
    mounted () {
        let ele = document.getElementById('app')
        ele.style.height = '100%'
    },
    destroyed () {
        let ele = document.getElementById('app')
        ele.style.height = ''
    }
}
</script>

<style scoped>
    .login-wrap{
        position: relative;
        width: 100%;
        height: 100%;
        background: rgba(56,157,170,.82)
    }
    .gm-title{
        position: absolute;
        top: 50%;
        width: 100%;
        margin-top: -220px;
        text-align: center;
        font-size: 30px;
        color: #fff
    }
    .gm-login{
        position: absolute;
        width: 320px;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        padding: 40px;
        border-radius: 5px;
        background: #fff
    }
    .gm-login > .lg-item {
        margin: 15px 0;
    }
</style>



